/* ChatWindow.css */

.chat-window {
    display: flex;
    /* justify-content: center; */
    /* align-items: flex-end; */
    width: calc(100% - 2px);
    height: 415px;
    overflow-y: scroll;
    /* border: 1px solid rgba(140, 140, 140, 0.35); */
}

.message-list {
    display: flex;
    flex-direction: column;
    padding: 15px;
}

.message-item {
    display: flex;
    align-items: center;
    padding: 8px;
    max-width: 90%;
    word-wrap: break-word;
    white-space: pre-wrap;
    /* animation: slide-in 0.5s ease-in-out; */
}

.text-container {
    flex-grow: 1;
    margin-left: 15px;
    padding: 10px;
    border: 1px solid rgba(140, 140, 140, 0.35);
}

.message-gray{
    color:gray
}

.message-green {
    color: green;
}

.message-orange {
    color: orange;
}

.message-red {
    color: red;
}

.robot-icon {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-right: 10px;
}

.message-text {
    flex-grow: 1;
}

.highlight {
    border: 1px solid rgba(140, 140, 140, 0.35);
    padding: 10px;
    /* border-radius: 0%; */
    /* background-color: gray; */
}

@keyframes slide-in {
    0% {
        opacity: 0;
        transform: translateX(-100%);
    }

    100% {
        opacity: 1;
        transform: translateX(0);
    }
}





