/* 这将设置对话框的透明度 */
.custom-modal .ant-modal-content {
    background-color: rgba(0, 0, 0, 0.5);
}
/* 这将设置底层遮罩的透明度 */
.custom-modal .ant-modal-mask {
    background-color: rgba(0, 0, 0, 0.5);
}
.custom-modal .ant-modal-header {
    background-color: rgba(0, 0, 0, 0.0);
}
.custom-modal .ant-modal-title {
    color: white; 
}