.container {
  display: flex;
  justify-content: center;
}

.menu {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  max-width: 768px;
  margin: 0 auto;
}

.menu-item {
  padding: 0;
  border: none;
  background: transparent;
}

.menu-item a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  text-decoration: none;
}

.menu-item a:hover,
.menu-item a:active,
.menu-item a:focus {
  background-color: transparent;
  outline: none;
}

.ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected {
  background-color: transparent;
  border-bottom: none;
}

.ant-menu-item-selected>a {
  color: white;
}





