@media (max-width: 768px) {
  .App-logo {
    width: 300px;
    height: 300px;
    pointer-events: none;
    margin-top: -160px;
    transform: rotate(360deg); /* 进行90度的旋转 */    
  }
  .login-box{
    /* border: 1px solid green; */
    margin-top: -40px;
    width: 210px;
  }
}

@media (min-width: 769px) {
  .App-logo {
    width: 300px;
    height: 300px;
    margin-top: -160px;
    pointer-events: none;
    /* transform: rotate(90deg); 进行90度的旋转 */
  }
  .login-box{
    /* border: 1px solid green; */
    margin-top: -40px;
    width: 210px;
  }
}

