.login{
  background-color:black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.form-item {
  margin-bottom: 5px;
}

.label-text {
  font-size: 14px; /* 调整标签文本的大小 */
  display: inline-block;
  margin-bottom: 7px;
}

.center-button {
  display: flex;
  justify-content: center;
}
.custom-form .ant-form-item-label label {
  color: white; /* 设置标签的文本颜色为白色 */
}
.App-link {
  color: #61dafb;
}













