/* 修改特定表格的表头字体大小 */
.my-custom-table .ant-table-thead > tr > th {
    font-size: 10px;
    text-align: center;
    font-weight: normal;
}

/* 修改特定表格的表项字体大小 */
.my-custom-table .ant-table-tbody > tr > td {
    font-size: 10px;
    text-align: center;
}

.ant-table-cell {
    padding: 8px !important;
}

/* 修改圆形进度条文本大小 */
.my-custom-table .ant-progress .ant-progress-inner .ant-progress-text {
    font-size: 9px !important;
}

/* 将 .ant-table-expanded-row 和它的所有子 td 的 padding 都设置为 0 */
.ant-table-expanded-row > td, 
.ant-table-expanded-row > td * {
    padding: 0 !important;
}

/* 或者你可能需要移除 margin */
.ant-table-expanded-row > td, 
.ant-table-expanded-row > td * {
    margin: 0 !important;
}
















